body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar-brand {
    font-size: 28px;
    margin-right: 25px;
    cursor: pointer;
}

.navbar-button {
    font-size: 18px;
}
.portfolio {
    text-align: center;
    padding-bottom: 50px;
}

.project {
    position: relative;
    max-width: 1000px;
    overflow: hidden;
    border-radius: 4px;
    border: 1px solid darkgray;
    margin: 25px auto 25px;
    text-align: center;
}

.project-title {
    font-size: 2em;
}

.project-text {
    font-size: 1.1em;
    padding: 10px;
}

.project-image {
    border: 1px solid gray;
    float: left;
}

.project-link {
    font-size: 1.5em;
}

.discontinued-projects {
    max-width: 1000px;
    margin: auto auto auto auto;
}

.discontinued-project {
    border: 1px solid darkgray;
    border-radius: 4px;
    max-width: 300px;
    float: left;
    margin-left: 25px;
}

.project-title-small {
    font-size: 1.25em;
}

.project-text-small {
    font-size: 1.05em;
}

.project-link-small {
    font-size: 1.25em;
}

.home {
    position: absolute;
    top: 50%;
    left: 40%;
    -webkit-transform: translate(-33%, -50%);
            transform: translate(-33%, -50%);
    text-align: center;
}

.home-name {
    text-transform: uppercase;
    font-weight: bold;
    font-size: 8em;
    line-height: 1em;
}

.home-title {
    font-size: 3em;
    font-weight: bolder;
}

.home-about {
    margin-left: 5%;
    margin-right: 5%;
    margin-top: 3%;
    text-align: center;
    font-size: 1.65em;
}

.home-action {
    font-size: 1.65em;
    margin-top: 3%;
}

.email-button {
    font-size: 1.5em;
    border-radius: 5px;
    border: 1px solid black;
    padding: 0.5em 0.8em;
}

.resume-button {

}

.resume-buttons {
    display: flex;
    justify-content: center;
}

